// 
import Vue from 'vue'
import VueRouter from 'vue-router'

// // 
// import Home from '@/components/HelloWorld';
import Dashboard from '@/components/dashboard';
import Login from '@/components/pages/Login';
import Products from '@/components/pages/products';
import CustomerOrder from '@/components/pages/CustomerOrder';
import CustomerCheckout from '@/components/pages/CustomerCheckout';
import MyStore from '@/components/pages/mystore';
import myStoreProduct from '@/components/pages/mystoreProduct';
import mystoreCartList from '@/components/pages/mystoreCartList';
import mystoreCheckout from '@/components/pages/mystoreCheckout';
// import mystoreLogin from '@/components/pages/mystoreLogin';


Vue.use(VueRouter);

export default new VueRouter({
    // mode:'history',
    // linkActiveClass:'active',
    routes:[
      // {
      //   path:'*',
      //   redirect:'mystoreLogin',
      // },
      

      {
        name:'',//
        path:'/login',//
        component:Login//
      },

      {
        name:'dashboard',//
        path:'/admin',//
        component:Dashboard,//
        children:[
          {
            name:'products',//
            path:'products',//
            component:Products,//
            meta:{ requiresAuth: true },
          },  
          
        ],

      },

      {
        name:'dashboard2',//
        path:'/admin',//
        component:Dashboard,//
        children:[
          {
            name:'CustomerOrder',//
            path:'customer_order',//
            component:CustomerOrder,//
          }, 
          {
            name:'CustomerCheckout',//
            path:'customer_checkout/:orderId',//
            component:CustomerCheckout,//
          }, 
          
        ],

      },
      // {
      //   name:'',//
      //   path:'/',//
      //   component:mystoreLogin//
      // },
      {
        name:'',//
        path:'/',//
        component:MyStore//
      },
      {
        name:'',//
        path:'/mystoreProduct',//
        component:myStoreProduct//
      },
      {
        name:'',//
        path:'/mystoreCartList',//
        component:mystoreCartList//
      },
      {
        name:'',//
        path:'/mystoreCheckout/:orderId',//
        component:mystoreCheckout//
      },

    ],
})


